@import url(https://fonts.googleapis.com/css?family=Poppins:400,700,900);
@import url(https://fonts.googleapis.com/css?family=Poppins:400,700,900);
.user-card-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 12px;
  margin-bottom: 30px;
  background-color: #354249;
  padding: 9px;

  -webkit-box-shadow: -30px 30px 30px rgba(0, 0, 0, 0.3);

          box-shadow: -30px 30px 30px rgba(0, 0, 0, 0.3);
}

.profile-pic {
  width: 48px;
  height: 48px;
  border-radius: 24px;
  /* border: 3px solid #000; */
}
.imgStyle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 3px;
  background-color: #fff;
  border-radius: 50%;
  border: 2px solid green;
  -webkit-box-shadow: 1px 10px 10px -4px #000;
          box-shadow: 1px 10px 10px -4px #000;
}
.user-details-container {
  margin-left: 12px;
}

.user-name {
  font-size: 18px;
  color: #000;
  font-family: 'Roboto';
  font-weight: bold;
  margin-top: 0px;
  margin-bottom: 8px;
}

.user-designation {
  font-family: 'Roboto';
  font-size: 14px;
  color: rgb(39, 216, 39);
  margin: 0px;
}

.delete-button {
  font-size: 30px;
  font-weight: 500;
  font-family: 'Roboto';
  color: #594d6d;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-right: 12px;
  padding-left: 12px;
  margin-left: 12px;
  background-color: transparent;
  border: none;
  border-radius: 4px;
}

.delete-img {
  width: 15px;
  height: 15px;
}

.deleteX {
  font-family: 'helvetica';
  /* font-weight: 900; */
  font-size: 25px;
  color: #000;
}

.app-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  background-color: #283035;
  min-height: 100vh;
}

.list-container {
  list-style-type: none;
  padding: 0;
}

.title {
  font-family: 'Poppins';
  font-size: 40px;
  color: #fff;
  font-weight: 500;
  margin-bottom: 30px;
}
.inputStyle {
  width: 200px;
  height: 35px;
  border-radius: 3px;
  margin-top: 10px;
  margin-bottom: 30px;
  background-color: transparent;
  border: 1px solid green;
  outline: none;
}

